import React, { useCallback, useRef, useState } from "react"
import { FormHandles } from "@unform/core"
import { Form } from "@unform/web"
import { Row, Col } from "react-bootstrap"
import * as Yup from "yup"
import { navigateTo } from "gatsby"

import { translate } from "../../translate"
import { Container, Button } from "./styles"
import Input from "../Input"
import Textarea from "../Textarea"
import getValidationErrors from "../../../../utils/getValidationErrors"
import CheckboxInput from "../Checkbox"
import { sendEmail } from "../../../../services/form-service"

interface ICheckboxOption {
	id: string
	value: string
	label: string
}

const FormContact: React.FC = () => {
	const formRef = useRef<FormHandles>(null)
	const [loading, setLoading] = useState(false)

	const checkboxOptionsContact: ICheckboxOption[] = [
		{
			id: "contactEmail",
			value: "Contato por e-mail",
			label: translate(`form.Gostaria de receber contato por`) + " e-mail",
		},
		{
			id: "contactTelefone",
			value: "Contato por telefone",
			label: translate(`form.Gostaria de receber contato por`) + " telefone",
		},
		{
			id: "contactWhatsapp",
			value: "Contato por WhatsApp",
			label: translate(`form.Gostaria de receber contato por`) + " WhatsApp",
		},
	]

	const checkboxIAgree: ICheckboxOption[] = [
		{
			id: "concordoFormulario",
			value:
				"Eu aceito receber e-mails com noticias, conte√∫dos e contatos da Kivalita Consulting",
			label: translate("form.i agree form"),
		},
	]

	const handleSubmit = async () => {
		const data = formRef.current?.getData()

		try {
			formRef.current?.setErrors({})

			const schema = Yup.object().shape({
				nome: Yup.string().required("Nome obrigat√≥rio"),
				email: Yup.string()
					.required("E-mail obrigat√≥rio")
					.email("Digite um e-mail v√°lido"),
				mensagem: Yup.string().required("Mensagem obrigat√≥ria"),
				empresa: Yup.string().required("Empresa obrigat√≥ria"),
				cargo: Yup.string().required("Cargo obrigat√≥ria"),
				telefone: Yup.string().required("Telefone obrigat√≥ria"),
				assunto: Yup.string().required("Assunto obrigat√≥ria"),
				concordoFormulario: Yup.string().required("Campo obrigat√≥rio"),
			})

			await schema.validate(data, { abortEarly: false })

			setLoading(true)

			await sendEmail(data)

			navigateTo("/thank-you")
		} catch (err) {
			setLoading(false)
			if (err instanceof Yup.ValidationError) {
				const errors = getValidationErrors(err)
				formRef.current?.setErrors(errors)

				return
			}

			navigateTo("/thank-you")
		}
	}

	return (
		<Form ref={formRef}>
			<Container className="containerForm">
				<input name="_formsubmit_id" type="text" style={{ display: "none" }} />
				<input
					name="_redirect"
					type="hidden"
					id="name"
					value="http://localhost:8000/"
				></input>
				<Row>
					<Col xs="12" md="12" lg="4">
						<label>{translate(`form.Nome Completo`)}</label>
						<Input name="nome" />
					</Col>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Empresa`)}</label>
						<Input name="empresa" />
					</Col>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Cargo`)}</label>
						<Input name="cargo" />
					</Col>
				</Row>

				<Row>
					<Col xs="12" md="12" lg="4">
						<label>{translate(`form.E-mail`)}</label>
						<Input type="email" name="email" />
					</Col>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Telefone`)}</label>
						<Input name="telefone" />
					</Col>
					<Col xs="12" md="6" lg="4">
						<label>{translate(`form.Telefone (Opcional)`)}</label>
						<Input name="telefoneOpcional" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<label>{translate(`form.Assunto`)}</label>
						<Input name="assunto" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<label>{translate(`form.message`)}</label>
						<Textarea name="mensagem" />
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<CheckboxInput
							name="tipoContato"
							options={checkboxOptionsContact}
						></CheckboxInput>
					</Col>
				</Row>

				<Row>
					<Col xs="12">
						<CheckboxInput
							name="concordoFormulario"
							options={checkboxIAgree}
						></CheckboxInput>
					</Col>
				</Row>

				<Button onClick={handleSubmit}>
					{loading
						? translate("form.Enviando")
						: translate("form.Enviar Contato")}
				</Button>
			</Container>
		</Form>
	)
}

export default FormContact
